import { JOB_FEATURED_LIST } from "@/api/api";
export default {
  name: "dailyJobMatch",

  data() {
    return {
      jobFeaturedData: []
    };
  },

  mounted() {
    this.getJobFeaturedList();
  },

  methods: {
    turnJobDetail(id) {
      this.$router.push({
        path: '/jobs',
        query: {
          id: id
        }
      });
    },

    getJobFeaturedList() {
      let params = {
        ad_type: 2
      };
      JOB_FEATURED_LIST(params).then(res => {
        console.log(res);

        if (res.code === 200) {
          this.jobFeaturedData = res.message;
        } else {
          console.log(res.msg);
        }
      }).catch(err => {
        console.log(err);
      });
    }

  }
};